body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.nav-wrapper {
  background: #222;
}
#nav-mobile {
  background: #222;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.search-input{
    width:300px;
    position: relative;

}

.query-label{
    font-size:20px;
}
.chat{
    position: fixed;
    width:400px;
    bottom: 0px;
    right: 0px;
}

.chat-box{
    border-style: inset;
    border-width: 1px;
    border-radius: 15px;
    position: relative;
    bottom: 0px;
    right: 0px;
    width:400px;
    height:400px;
    background-color: white;
}

.chat-bar{
    position: absolute;
    height:50px;
    width:400px;
    top:-50px;
    border-width: 2px;
    border-style:groove;
    border-radius: 15px;
    background-color: black;
}

.chat-toggle-btn{
    position: absolute;
    right:0px;
    margin-top: 5px;
    margin-right: 5px;
    border-radius: 10px;
    color:black;
}

#chat-input{
    position: absolute;
    width:80%;
    height: 30px;
    font-size: 20px;
    bottom: 0px;
    padding:0px; 
    border-radius: 5px;
    border-style:solid;
    border-width:1px;
    left: 10px;
    background-color: lightgray;
}

#chat-send-btn{
    position: absolute;
    width:15%;
    height:35px;
    font-size: 20x;
    bottom:10px;
    right:0px;
    border-radius:5px;
}

.chat-message{
    list-style-type:none;
    margin-left:10px;
    text-align: left;
    margin-bottom: 0px;
    margin-top:5px;
}

.bar-title{
    color:white;
}


